html {
  font-size: 62.5%;
  overflow-x: hidden;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
}
@media (min-width: 992px) {
  html html {
    font-size: 55%;
  }
}
@media (min-width: 480px) {
  html html {
    font-size: 50%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

*::selection {
  background: #c72727;
  color: #fff;
}

body {
  background: #f0f0f0;
  color: #3c3c3c;
  font-size: 1.6rem;
}
body.bodyLock {
  overflow: hidden;
}

section {
  padding: 2rem 9%;
}
@media (max-width: 992px) {
  section {
    padding: 2rem;
  }
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

li {
  list-style-type: none;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  padding: 1rem 9%;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}
.header .logo img {
  width: 80px;
  padding-right: 0.5rem;
}
@media (max-width: 768px) {
  .header .logo img {
    width: 80px;
  }
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.navbar ul {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  gap: 3rem;
}
@media (max-width: 992px) {
  .navbar ul {
    display: none;
    flex-direction: column;
    width: 100%;
  }
}
.navbar.active {
  flex-wrap: wrap;
  transition: all 0.2s linear;
}
.navbar.active ul {
  padding: 3rem 0;
  display: flex;
}

ul li a {
  display: inline-block;
  font-size: 1.8rem;
  color: #3c3c3c;
}
@media (max-width: 768px) {
  ul li a {
    padding: 10px;
  }
}
ul li a:hover {
  color: #c72727;
}

/* gemeric button */
.btn {
  font-weight: 600;
  padding: 1.5rem 3.5rem;
  background: #c72727;
  color: #fff;
  border-radius: 25rem;
  border: 2px solid transparent;
}
.btn:hover {
  background: transparent;
  border: 2px solid #c72727;
}

/* hamburger button */
.menu-btn {
  position: relative;
  display: none;
  width: 80px;
  height: 80px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  /* border: 3px solid #fff; */
}
@media (max-width: 992px) {
  .menu-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.menu-btn__burger {
  width: 50px;
  height: 6px;
  background: #0f0f0f;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
}

.menu-btn__burger::before,
.menu-btn__burger::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 6px;
  background: #0f0f0f;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
}

.menu-btn__burger::before {
  transform: translateY(-16px);
}

.menu-btn__burger::after {
  transform: translateY(16px);
}

/* ANIMATION */
.menu-btn.open .menu-btn__burger {
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}

.menu-btn.open .menu-btn__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}

.menu-btn.open .menu-btn__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.home {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  min-height: 100vh;
  align-items: center;
  background: url(/build/img/home-bg.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.home .content {
  flex: 1 1 40rem;
  padding-top: 6.5rem;
}
.home .content h3 {
  font-size: 7rem;
  margin-bottom: 2rem;
  color: #3c3c3c;
}
.home .content h3 span {
  color: #c72727;
}
.home .content p {
  font-size: 2rem;
  color: #666666;
  padding: 1rem 0;
  margin-bottom: 4rem;
  line-height: 2.5rem;
}
.home .content .btn {
  margin-top: 3rem;
}
.home .content .btn:hover {
  color: #c72727;
}
.home .image {
  flex: 1 1 40rem;
}
.home .image img {
  width: 100%;
  padding: 1rem;
  animation: float 3s linear infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(1rem);
  }
}
.log-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  align-items: center;
  background: url(/build/img/home-bg.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  column-gap: 3rem;
}

.form {
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 6rem;
  margin-top: 3rem;
  width: 80%;
}
@media (min-width: 768px) {
  .form {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .form {
    width: 40%;
  }
}
@media (min-width: 1200px) {
  .form {
    width: 35%;
  }
}
.form .field {
  height: 5rem;
  width: 100%;
  margin-top: 3rem;
  position: relative;
}
.form .field input, .form .field button {
  height: 100%;
  width: 100%;
  font-size: 1.4rem;
  font-weight: 400;
}
.form .field input {
  border: 1px solid #666666;
  padding: 0 1.5rem;
  border-radius: 0.5rem;
  outline: none;
  text-transform: none;
}
.form .field input[type=checkbox] {
  padding: 0;
  border: none;
}
.form .field button {
  border-radius: 3rem;
  background-color: #c72727;
  color: #fff;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}
.form .field button:hover {
  border: 2px solid #c72727;
  background-color: transparent;
  color: #c72727;
}
.form .field .eye-icon {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  padding: 0.5rem;
  font-size: 1.8rem;
  color: #c72727;
}
.form .field .check-icon {
  visibility: hidden;
  position: absolute;
  top: 50%;
  right: -3rem;
  transform: translateY(-50%);
  color: #4bb417;
  font-size: 2rem;
}
.form .field .error-icon {
  visibility: hidden;
  position: absolute;
  top: 50%;
  right: -3rem;
  transform: translateY(-50%);
  color: #c72727;
  font-size: 2rem;
}
.form .field small {
  font-size: 1rem;
  color: #c72727;
  position: absolute;
  bottom: -1.8rem;
  left: 0;
  visibility: hidden;
  text-transform: none;
}
.form .field.correct input {
  border: 1px solid #4bb417;
}
.form .field.correct .check-icon {
  visibility: visible;
}
.form .field.error input {
  border: 1px solid #c72727;
}
.form .field.error .error-icon {
  visibility: visible;
}
.form .field.error small {
  visibility: visible;
}
.form .form-link {
  text-align: center;
  margin-top: 2.5rem;
}
.form .form-link span, .form .form-link a {
  font-size: 1.2rem;
  font-weight: 400;
}
.form a {
  color: #c72727;
}
.form a:hover {
  text-decoration: underline;
}

.check-input {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #3c3c3c;
  font-size: 1.4rem;
  margin-top: -0.5rem;
}
.check-input:active {
  color: #3c3c3c;
}
.check-input #TOS-check {
  width: 12px;
}

.form.signup {
  opacity: 0;
  pointer-events: none;
}

.form.login {
  opacity: 1;
  pointer-events: auto;
}

.forms.show-signup .form.signup {
  opacity: 1;
  pointer-events: auto;
  transition: all 0.1s linear;
}
.forms.show-signup .form.login {
  opacity: 0;
  pointer-events: none;
  transition: all 0.1s linear;
}

.login .line, .signup .line {
  position: relative;
  height: 1px;
  width: 100%;
  background-color: #666666;
  margin: 3.6rem 0;
}
.login .line::before, .signup .line::before {
  content: "Or";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  color: #3c3c3c;
  padding: 0 1.5rem;
}
.login .media-options a, .signup .media-options a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: 1.4rem;
  color: #fff;
}
.login .media-options a:hover, .signup .media-options a:hover {
  text-decoration: none;
  transform: scale(1.05);
}
.login .media-options .field, .signup .media-options .field {
  border-radius: 3rem;
}
.login .media-options .facebook, .signup .media-options .facebook {
  background-color: #4267b2;
}
.login .media-options .google, .signup .media-options .google {
  margin-top: 1.2rem;
  background-color: transparent;
  border: 1px solid #666666;
  color: #c72727;
}
.login .media-options .google span, .signup .media-options .google span {
  color: #666666;
  font-weight: 500;
}
.login .media-options .media-icon, .signup .media-options .media-icon {
  font-size: 2rem;
}

header {
  font-size: 3rem;
  font-weight: 700;
  color: #3c3c3c;
  text-align: center;
}

h1 {
  padding-top: 1rem;
  color: #c72727;
}

h4 {
  text-transform: none;
}

p {
  text-transform: none;
}

.reservation_general_container {
  width: 100%;
  align-items: center;
}

.reservationForm_mainbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 2rem;
  margin-top: 15%;
  margin-right: 20%;
  margin-left: 20%;
  background: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  padding: 3rem 0 2rem 0;
  margin-bottom: 3rem;
}

.title_of_mainbox {
  padding-top: 3%;
  padding-left: 10%;
  padding-right: 10%;
  font-size: 1.8rem;
  color: #3c3c3c;
}

.content_of_mainbox {
  margin: 0 auto;
  width: 65%;
  padding: 1rem;
}
.content_of_mainbox form {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 1.5rem;
}

.input_date_reservation_box {
  border: 1px solid #666666;
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
  outline: none;
  margin-bottom: 2rem;
}

.reservationTextBox {
  border: 1px solid #666666;
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
  outline: none;
  text-transform: none;
}

.reservationButton {
  font-weight: 600;
  padding: 1.5rem 3.5rem;
  margin: 0 auto;
  background: #c72727;
  color: #fff;
  border-radius: 25rem;
  border: 2px solid transparent;
  align-self: center;
}
.reservationButton:hover {
  background: transparent;
  border: 2px solid #c72727;
  color: #c72727;
}